import React from 'react'

const Card = props => (
    <div className="rounded overflow-hidden shadow-lg bg-white">
        <img src={props.image} alt={props.alt} />
        <h3 className="font-bold lg:text-lg md:text-md mb-2 pt-4 px-4">{props.title}</h3>
        <p className="pb-4 px-4 lg:text-lg md:text-md">{props.text}</p>
    </div>
)

export default Card